<template>
  <div class="pedido container py-5">
    <div
      id="estadoTienda"
      class="alert alert-info text-center topbar"
      role="alert"
    >
      {{ estado }}
    </div>

    <div class="card mx-auto" style="max-width: 600px">
      <div class="card-header">Pedido de Pollos Dumbo</div>
      <div class="card-body">
        <form @submit.prevent="enviarPedido">
          <!-- Nombre -->
          <div class="mb-3">
            <label class="form-label">Nombre</label>
            <input v-model="nombre" class="form-control" required />
          </div>

          <!-- Dirección -->
          <div class="mb-3">
            <label class="form-label">Dirección</label>
            <input v-model="direccion" class="form-control" required />
          </div>

          <!-- Teléfono -->
          <div class="mb-3">
            <label class="form-label">Teléfono</label>
            <input v-model="telefono" class="form-control" required />
          </div>

          <!-- Producto -->
          <div class="mb-3">
            <label class="form-label">Producto</label>
            <select v-model="producto" class="form-select">
              <option value="">Seleccione...</option>
              <option value="entero">Pollo Entero</option>
              <option value="broaster">Pollo Broaster</option>
              <option value="frito">Pollo Frito</option>
            </select>
          </div>

          <!-- Porción -->
          <div class="mb-3">
            <label class="form-label">Porción</label>
            <select v-model="porcion" class="form-select">
              <option value="">Ninguna</option>
              <option value="arroz">Arroz</option>
              <option value="papa">Papa frita</option>
              <option value="fideo">Fideo</option>
              <option value="salsa">Salsa</option>
            </select>
          </div>

          <!-- Refresco -->
          <div class="mb-3">
            <label class="form-label">Refresco</label>
            <select v-model="refresco" class="form-select">
              <option value="">Ninguno</option>
              <option value="maracuya">Maracuyá</option>
              <option value="limonada">Limonada</option>
              <option value="gaseosa">Gaseosa</option>
            </select>
          </div>

          <!-- Gaseosa marca/tamaño -->
          <div v-if="refresco === 'gaseosa'" class="row g-2 mb-3">
            <div class="col-sm-6">
              <label class="form-label">Marca</label>
              <select v-model="gaseosaMarca" class="form-select">
                <option value="coca">Coca-Cola</option>
                <option value="fanta">Fanta</option>
                <option value="sprite">Sprite</option>
              </select>
            </div>
            <div class="col-sm-6">
              <label class="form-label">Tamaño</label>
              <select v-model="gaseosaTam" class="form-select">
                <option value="mini">Mini</option>
                <option value="popular">Popular</option>
                <option value="mediana">Mediana</option>
              </select>
            </div>
          </div>

          <!-- Pago -->
          <div class="mb-3">
            <label class="form-label">Método de pago</label>
            <div>
              <label class="me-3">
                <input type="radio" value="caja" v-model="pago" /> Caja
              </label>
              <label>
                <input type="radio" value="qr" v-model="pago" /> QR
              </label>
            </div>
          </div>

          <!-- Panel QR -->
          <div v-if="pago === 'qr'" class="mb-3 text-center">
            <img
              :src="base + 'img/QR.jpg'"
              alt="QR de pago"
              class="img-fluid"
              style="max-width: 200px"
            />
            <input
              v-model="qrRef"
              class="form-control mt-2"
              placeholder="Referencia (opcional)"
            />
          </div>

          <!-- Total -->
          <div class="alert alert-warning fw-bold">
            Total estimado: Bs {{ total.toFixed(2) }}
          </div>

          <!-- Botones -->
          <div class="d-grid gap-2">
            <button type="submit" class="btn btn-primary">Enviar pedido</button>
            <button type="button" @click="enviarWhatsApp" class="btn btn-success">
              Enviar por WhatsApp
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import CATALOGO from "@/data/catalogo";

export default {
  name: "PedidoView",
  data() {
    return {
      estado: "Cargando horario…",
      base: process.env.BASE_URL,
      nombre: "",
      direccion: "",
      telefono: "",
      producto: "",
      porcion: "",
      refresco: "",
      gaseosaMarca: "coca",
      gaseosaTam: "popular",
      pago: "caja",
      qrRef: "",
    };
  },
  computed: {
    total() {
      const { pollo, porciones, bebidas } = CATALOGO;
      let t = 0;
      if (this.producto) t += pollo[this.producto] || 0;
      if (this.porcion) t += porciones[this.porcion] || 0;
      if (this.refresco === "gaseosa") {
        t += bebidas.gaseosa[this.gaseosaMarca]?.[this.gaseosaTam] || 0;
      } else if (this.refresco) {
        t += bebidas[this.refresco] || 0;
      }
      return t;
    },
  },
  mounted() {
    // Estado abierto/cerrado
    const horario = { open: "15:00", close: "21:00" };
    const toMin = (s) => {
      const [h, m] = s.split(":").map(Number);
      return h * 60 + m;
    };
    const now = new Date(),
      mins = now.getHours() * 60 + now.getMinutes();
    const abierto = mins >= toMin(horario.open) && mins <= toMin(horario.close);
    this.estado = `${abierto ? "ABIERTO" : "CERRADO"} • ${
      horario.open
    }–${horario.close} (Llallagua)`;

    // Leer query params (para precargar producto/refresco)
    if (this.$route.query.producto) {
      this.producto = this.$route.query.producto;
    }
    if (this.$route.query.refresco) {
      this.refresco = "gaseosa"; // simplificado
    }
  },
  methods: {
    enviarPedido() {
      alert("Pedido enviado (demo)");
    },
    enviarWhatsApp() {
      const texto = `*Pedido* %0ACliente: ${this.nombre}%0AProducto: ${
        this.producto || "-"
      }%0ATotal: Bs ${this.total.toFixed(2)}`;
      const numero = "59174148930";
      window.open(`https://wa.me/${numero}?text=${texto}`, "_blank");
    },
  },
};
</script>
