<template>
  <div class="home">
    <!-- Badge de horario -->
    <div class="container pt-3">
      <div
        id="estadoTienda"
        class="alert alert-info text-center py-2 mb-0"
        role="alert"
      >
        {{ estado }}
      </div>
    </div>

    <!-- Centro -->
    <div class="hero-wrap">
      <div class="container cta-zone" style="max-width: 900px">
        <img :src="base + 'img/escudo.png'" alt="Pollos Dumbo" class="logo" />
        <h1 class="brand">Pollos Dumbo</h1>
        <p class="subtitle">¡El mejor pollo de Llallagua!</p>
        <p class="copy">
          Abrimos de <strong>3:00 pm</strong> a <strong>9:00 pm</strong>. Pide
          para delivery o pasa a recoger.
        </p>

        <!-- Botones -->
        <div class="d-flex justify-content-center gap-3 flex-wrap mt-3">
          <RouterLink :to="{ name: 'pollo' }" class="btn btn-brand btn-lg px-4">
            Ver menú
          </RouterLink>
          <RouterLink
            :to="{ name: 'pedido' }"
            class="btn btn-outline-dark btn-lg px-4"
          >
            Hacer pedido
          </RouterLink>
        </div>
      </div>
    </div>

    <footer>
      © {{ new Date().getFullYear() }} Pollos Dumbo • Llallagua
    </footer>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      estado: "Cargando horario…",
      base: process.env.BASE_URL,
    };
  },
  mounted() {
    const horario = { open: "15:00", close: "21:00" };
    const toMin = (s) => {
      const [h, m] = s.split(":").map(Number);
      return h * 60 + m;
    };
    const now = new Date(),
      mins = now.getHours() * 60 + now.getMinutes();
    const abierto = mins >= toMin(horario.open) && mins <= toMin(horario.close);
    this.estado = `${abierto ? "ABIERTO" : "CERRADO"} • ${
      horario.open
    }–${horario.close} (Llallagua)`;
  },
};
</script>

<style scoped>
.hero-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 32px 16px;
}
.logo {
  width: 160px;
  margin-bottom: 10px;
}
.brand {
  font-family: "Luckiest Guy", cursive;
}
</style>
