<template>
  <div class="menu container py-5">
    <!-- Badge -->
    <div
      id="estadoTienda"
      class="alert alert-info py-2 px-3 mb-4 text-center"
      role="alert"
    >
      {{ estado }}
    </div>

    <!-- Logo y botones -->
    <header class="text-center mb-4">
      <img
        :src="base + 'img/escudo.png'"
        alt="Pollos Dumbo"
        style="height: 100px; width: auto"
      />
      <h1 class="mt-2 brand-text">Pollos Dumbo</h1>
      <div class="d-flex justify-content-center gap-3 flex-wrap">
        <RouterLink to="/" class="btn btn-light btn-lg fw-bold px-4">
          Inicio
        </RouterLink>
        <RouterLink :to="{ name: 'pedido' }" class="btn btn-dark btn-lg fw-bold px-4">
          Hacer Pedido
        </RouterLink>
      </div>
    </header>

    <!-- Sección comidas -->
    <h2 class="text-center mb-4 brand-text section-title">Nuestro Menú</h2>
    <div class="row">
      <div v-for="p in productos" :key="p.key" class="col-sm-6 col-lg-4">
        <div class="card mb-4 shadow-sm h-100">
          <img :src="base + p.img" class="card-img-top" :alt="p.nombre" />
          <div class="card-body d-flex flex-column">
            <h5 class="card-title">{{ p.nombre }}</h5>
            <p class="card-text text-muted">{{ p.desc }}</p>
            <div class="mt-auto d-flex justify-content-between">
              <span class="fw-bold">Bs {{ p.precio.toFixed(2) }}</span>
              <RouterLink
                :to="{ name: 'pedido', query: { producto: p.key } }"
                class="btn btn-sm btn-primary"
              >
                Pedir
              </RouterLink>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Sección bebidas -->
    <h2 class="text-center mt-5 mb-4 brand-text section-title">
      Bebidas Gaseosas
    </h2>
    <div class="row">
      <div v-for="b in bebidas" :key="b.nombre" class="col-6 col-lg-4">
        <div class="card mb-4 shadow-sm h-100">
          <img :src="base + b.img" class="card-img-top drink-img" :alt="b.nombre" />
          <div class="card-body d-flex flex-column">
            <h5 class="card-title">{{ b.nombre }}</h5>
            <p class="card-text text-muted">Refresco gaseoso</p>
            <div class="mt-auto d-flex justify-content-between">
              <span class="fw-bold">Bs {{ b.precio.toFixed(2) }}</span>
              <RouterLink
                :to="{ name: 'pedido', query: { refresco: b.nombre } }"
                class="btn btn-sm btn-outline-success"
              >
                Pedir
              </RouterLink>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CATALOGO from "@/data/catalogo";

export default {
  name: "PolloView",
  data() {
    return {
      estado: "Cargando horario…",
      base: process.env.BASE_URL,
      productos: [
        {
          key: "entero",
          nombre: "Pollo Entero Frito",
          desc: "Ideal para compartir.",
          img: "img/entero.PNG",
          precio: CATALOGO.pollo.entero,
        },
        {
          key: "broaster",
          nombre: "Pollo Broaster",
          desc: "Crujiente y sabroso.",
          img: "img/broaster.jpg",
          precio: CATALOGO.pollo.broaster,
        },
        {
          key: "frito",
          nombre: "Pollo Frito Tradicional",
          desc: "Receta clásica de la casa.",
          img: "img/spiedo.jpg",
          precio: CATALOGO.pollo.frito,
        },
        {
          key: "arroz",
          nombre: "Porción de Arroz",
          desc: "Arroz blanco.",
          img: "img/arroz.PNG",
          precio: CATALOGO.porciones.arroz,
        },
        {
          key: "papa",
          nombre: "Papa Frita",
          desc: "Porción de papas.",
          img: "img/papita.png",
          precio: CATALOGO.porciones.papa,
        },
        {
          key: "fideo",
          nombre: "Porción de Fideo",
          desc: "Fideos al dente.",
          img: "img/fideo.PNG",
          precio: CATALOGO.porciones.fideo,
        },
      ],
      bebidas: [
        {
          nombre: "Coca-Cola Popular",
          img: "img/coca_popular.png",
          precio: CATALOGO.bebidas.gaseosa.coca.popular,
        },
        {
          nombre: "Fanta Popular",
          img: "img/fanta_flaca.webp",
          precio: CATALOGO.bebidas.gaseosa.fanta.popular,
        },
        {
          nombre: "Sprite Popular",
          img: "img/sprite_popular.webp",
          precio: CATALOGO.bebidas.gaseosa.sprite.popular,
        },
      ],
    };
  },
  mounted() {
    const horario = { open: "15:00", close: "21:00" };
    const toMin = (s) => {
      const [h, m] = s.split(":").map(Number);
      return h * 60 + m;
    };
    const now = new Date(),
      mins = now.getHours() * 60 + now.getMinutes();
    const abierto = mins >= toMin(horario.open) && mins <= toMin(horario.close);
    this.estado = `${abierto ? "ABIERTO" : "CERRADO"} • ${
      horario.open
    }–${horario.close} (Llallagua)`;
  },
};
</script>

<style scoped>
.card-img-top {
  object-fit: cover;
  height: 180px;
}
.drink-img {
  object-fit: contain;
  height: 160px;
  background: #fff;
}
.brand-text {
  font-family: "Luckiest Guy", cursive;
}
.section-title {
  font-size: clamp(26px, 4vw, 40px);
}
</style>
